import {RouteObject, useRoutes} from 'react-router-dom';
import {ShareableLinkPage} from './shareable-link/shareable-link-page/shareable-link-page';
import {DriveLayout} from './layout/drive-layout';
import {AuthRoute} from '@common/auth/guards/auth-route';
import {NotFoundPage} from '@common/ui/not-found-page/not-found-page';
import {WorkspaceLayout} from './layout/workspace-layout';
import { SupportticketLayout } from './layout/supportticketlayout';
import { IntegrationLayout } from './layout/Integrationlayout';

const DriveRouteConfig: RouteObject[] = [
  {
    path: '/',
    element: (
      <AuthRoute>
        <DriveLayout />
      </AuthRoute>
    ),
  },
  {
    path: '/folders/:hash',
    element: (
      <AuthRoute>
        <DriveLayout />
      </AuthRoute>
    ),
  },
  {
    path: '/shares',
    element: (
      <AuthRoute>
        <DriveLayout />
      </AuthRoute>
    ),
  },
  {
    path: '/recent',
    element: (
      <AuthRoute>
        <DriveLayout />
      </AuthRoute>
    ),
  },
  {
    path: '/starred',
    element: (
      <AuthRoute>
        <DriveLayout />
      </AuthRoute>
    ),
  },
  {
    path: '/trash',
    element: (
      <AuthRoute>
        <DriveLayout />
      </AuthRoute>
    ),
  },
  {
    path: '/search',
    element: (
      <AuthRoute>
        <DriveLayout />
      </AuthRoute>
    ),
  },
  {
    path: '/workspace',
    element: (
      <AuthRoute>
        <WorkspaceLayout/>
      </AuthRoute>
    ),
  },
  // {
  //   path: '/supportticket',
  //   element: (
  //     <AuthRoute>
  //       <SupportticketLayout/>
  //     </AuthRoute>
  //   ),
  // },
  // {
  //   path: '/integration',
  //   element: (
  //     <AuthRoute>
  //       <IntegrationLayout/>
  //     </AuthRoute>
  //   ),
  // },
  {path: 's/:hash', element: <ShareableLinkPage />},
  {path: '*', element: <NotFoundPage />},
];

export default function DriveRoutes() {
  return useRoutes(DriveRouteConfig);
}
